*{
    padding:0;
    margin:0;
    box-sizing: border-box;
}
.spinner{
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 9999;
    background-color: rgb(59, 59, 59);
}
.from-btn{
    width: 250px!important;
    margin-top: 30px!important;
}
.main{
    padding: 20px;
    height: 100vh;
    
}
.layout{
    display: flex;
}
.sidebar{
    min-height: 100%;
    width: 300px;
    border-radius: 5px;
    background-color: brown;
    box-shadow:  0 0 2px gray;
    margin-right: 10px;
    color: white;
    
}
.content{
    width: 100%;
    height: 100%;
}
.header{
    height: 10vh;
    margin-bottom: 20px;
    box-shadow: 0 0 2px gray;
    background-color: white;
}
.body{
    height: 85vh;
    margin-bottom: 20px;
    box-shadow: 0 0 2px gray;
    background-color: white;  
}
.logo h6{
    font-size: 1rem;
    text-align: center;
    margin: 20px 0px;
    padding: 10px;
}
.menu-item{
    margin-top: 30px;
}
.menu-item a{
    color: white;
    text-decoration: none;
    font-size: 1.2rem;
}
.menu-item i{
    font-size: 1.2rem;
    margin: 0 15px;
}
.active{
    background-color: #fff;
    padding: 10px;
}
.active a , .active i{
    color: rgb(51, 1, 1);
}
.header-content{
    display: flex;
    align-items: center;
    justify-content: flex-end;
    height: 33px;
    margin:0 15px
}
.header-content i{
    margin-right: 20px;
    font-size: 1.2rem;
}
.header-content a{
    text-decoration: none;
    font-size: 1.2rem;
    color: blue;
    margin: 0 10px;
text-transform: uppercase;    
}


.form-container{
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
}
.register-form{
    border:1px solid gray;
    padding:30px;
    width: 30%;
}
